.add-button {
  --_g: linear-gradient(var(--yellow) 0 0) no-repeat;
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 80px;
  height: 80px;
  z-index: 1;
  border: 3px var(--yellow) solid;
  box-shadow: 0 0 14px var(--yellow);
  border-radius: 80px;
  color: var(--yellow);
  font-weight: 800;
  font-size: 50px;
  z-index: 20;
  cursor: pointer;
  user-select: none;
  outline: none;
  background:
    var(--_g) calc(var(--_p, 0%) - 100%) 0%,
    var(--_g) calc(200% - var(--_p, 0%)) 0%,
    var(--_g) calc(var(--_p, 0%) - 100%) 100%,
    var(--_g) calc(200% - var(--_p, 0%)) 100%;
  background-size: 50.5% calc(var(--_p, 0%) / 2 + 0.5%);
  background-color: var(--black);
  outline-offset: 0.1em;
  transition:
    background-size 0.2s,
    background-position 0s 0.2s;
}

.add-button:hover {
  --_p: 100%;
  color: var(--black);
  box-shadow: 0 0 22px var(--yellow);
  transition:
    background-position 0.2s,
    background-size 0s;
}

.add-button:active {
  box-shadow: 0 0 9e9Q inset #0009;
  background-color: var(--yellow);
  color: #fff;
}

@media (max-width: 768px) {
  .add-button {
    background-color: var(--yellow);
    color: var(--black);
  }
}
