.to-do {
  background-color: var(--black);
  position: relative;
  display: flex;
  top: 0;
  justify-content: center;
  align-items: center;
  max-width: 1024px;
  min-width: 100%;
  padding: 20px 50px;
  border: 2px var(--yellow) solid;
  border-radius: 10px;
  text-align: center;
  transition: all 0.3s ease-in;
  overflow: hidden;
}

.to-do:before {
  content: '';
  position: absolute;
  z-index: 10;
  right: 18px;
  height: 32px;
  width: 32px;
  border-radius: 32px;
  transform: scale(1);
  transform-origin: 50% 50%;
  transition: transform 0.25s ease-out;
}

.to-do--completed {
  color: var(--black);
  text-decoration: line-through;
  background-color: var(--yellow);
}

.to-do:hover:before {
  transform: scale(50);
  background: var(--yellow);
}

.to-do:hover {
  cursor: pointer;
  transition: all 0.5s ease-out;
  box-shadow: 0 0 20px rgba(255, 213, 45, 0.6);
  top: -4px;
  color: var(--black);
}

.to-do__text {
  z-index: 20;
}
